* {
    margin: 0;
    padding: 0;
    scroll-behavior: smooth; }

body {
    overflow-x: hidden; }

.container {
    width: 1160px;
    margin: 0 auto;
    height: 100%;
    min-width: 1160px; }

.row {
    display: flex; }


/* Additional Header */

.additional-header {
    width: 100%;
    min-width: 1160px;
    height: 30px;
    border-top: 3px solid #cc3333;
    background-color: #2a2b2c; }

.icons {
    margin-top: 3px;
    & img {
        margin-left: 5px;
        cursor: pointer;
        opacity: .7; }
    & img:hover {
        opacity: 1; } }


.additional-header-container {
    display: flex;
    justify-content: space-between; }



.link-button {
    font-family: 'Verdana';

    display: flex;
    height: 30px;
    align-items: center;
    p {
        color: white;
        margin: 0 5px;
        font-size: 12px; } }

.link {
    color: white;
    text-decoration: none;
    font-size: 12px;
    &:hover {
        text-decoration: underline; } }



.card-btn {
    position: relative;
    cursor: pointer;
    transition: all .5s ease;
    margin-left: 50px;
    display: flex;
    background-color: #cc3333;
    border: none;
    outline: none;
    justify-content: space-around;
    height: 100%;
    width: 110px;
    align-items: center;
    & p {
        color: white; }
    & img {
        width: 25px;
        height: 25px; }
    &:hover {
        background-color: #a61e1e; } }



/* Main Header */
.main-header {
    width: 100%;
    min-width: 1160px;
    background-image: url('http://localhost:3000/images/background.png');
    background-size: cover;
    background-repeat: repeat;
    height: 90px; }

.main-header-container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
        cursor: pointer; } }


.main-header-links {
    list-style: none;
    display: flex;
    & li {
        font-size: 18px;
        font-weight: 600;
        font-family: 'Oswald';
        text-transform: uppercase;
        margin-left: 80px;
        border: 2px solid transparent;
        padding: 5px;
        cursor: pointer;
        transition: all .5s ease; }


    & li:hover {
        border: 2px solid black;
        border-radius: 10px;
        padding: 5px; } }


/* Image Section */

.image-section {
    display: flex;
    align-items: center;
    width: 100%;
    min-width: 1160px;
    height: 500px;
    border: 1px solid gray;
    background-color: #1e1e20; }

.image-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-x: hidden;
    img {
        height: 420px !important;
        width: 920px !important; } }



.arrow-btn-container {
    display: flex;
    position: absolute;
    justify-content: space-between;
    width: 100%;
    min-width: 1160px;
    button {
        border-radius: 100%;
        width:  75px;
        height: 75px;
        outline: none;
        border: none;
        transition: all .5s ease;
        background-color: rgba(0, 0, 0, .7);
        z-index: 10; }
    button:hover {
        transform: scale(1.2); }

    .left-btn {
        margin-left: 110px; }
    .right-btn {
        margin-right: 110px; } }


.card-slider {
    position: relative;
    margin: 0 auto;
    max-width: 920px; }


.card-slider-wrapper {
    display: flex;
    position: relative;
    transition: transform .7s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    img {
        flex: 1;
        margin: 15px;
        opacity: 0.3;
        transition: opacity .3s linear, transform .7s cubic-bezier(0.455, 0.03, 0.515, 0.955), all .7s ease;
        transform: scale(0.7);
        position: relative;
        box-shadow: 0 0 0 black; }



