/* Body Heading */
.body-heading {
    width: 100%;
    min-width: 1160px;
    background-image: url('http://localhost:3000/images/background.png');
    background-size: cover;
    background-repeat: repeat;
    height: 450px; }


.body-heading-container {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 42px;
    font-weight: 400;
    font-family: 'Oswald', sans-serif !important;
    text-transform: uppercase;
    span {
        background: #cd3737;
        padding: 0 6px;
        color: white;
        border-radius: 2px; }

    img {
        margin: 15px 0; } }


.big-btn-item {
    cursor: pointer;
    transition: all .4s ease;
    text-justify: left;
    padding: 15px;
    color: white;
    width: 315px;
    height: 90px;
    background-color: #1e1e20;
    font-family: 'Oswald', sans-serif !important;
    text-align: center;
    border-radius: 5px;
    div {
        display: flex;
        justify-content: space-around;
        margin-bottom: 12px;
        p {
            font-size: 20px; } }
    &:hover {
        background-color: #2c2c4b; } }

.big-buttons {
    margin-top: 70px;
    display: flex;
    justify-content: space-between; }
.red {
    background-color: #cc3333 !important;
    &:hover {
        background-color: #9b2828 !important; } }

.upcoming {
    div {
        justify-content: start;
        img {
            margin-right: 40px;
            margin-left: 20px; } } }


/* Latest Arrivals and Albums Currently and Our Publishers section headers */

.latest-arrival-section {
    width: 100%;
    min-width: 1160px;
    height: 615px;
    background-image: url('http://localhost:3000/images/background.png');
    background-size: cover;
    background-repeat: repeat; }



.section-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    p {
        font-family: 'Oswald';
        white-space: nowrap;
        text-transform: uppercase;
        font-size: 28px; }

    button {
        margin-left: 5px;
        background-color: #cc3333;
        border-radius: 5px;
        padding: 6px;
        border: none;
        outline: none;
        color: white;
        transition: all .2s ease;
        cursor: pointer;
        &:hover {
            background-color: #9b2828; } } }

.section-line-img {
    width: 67%;
    overflow: hidden; }


.albums-currently-section {
    width: 100%;
    min-width: 1160px;
    height: 615px;
    background-image: url('http://localhost:3000/images/background.png');
    background-size: cover;
    background-repeat: repeat; }



/* Important Publisher Section */
.important-publisher-section {
    width: 100%;
    min-width: 1160px;
    height: 350px;
    background-image: url('http://localhost:3000/images/background.png');
    background-size: cover;
    background-repeat: repeat; }

.publisher-container {
    padding-top: 30px;
    display: flex;
    justify-content: center;
    img {
        width: 17%;
