/* Main Footer */
.main-footer {
    position: relative;
    width: 100%;
    min-width: 1160px;
    height: 350px;
    background-image: url('http://localhost:3000/images/background_footer.png');
    background-size: cover;
    background-repeat: repeat;
    border-top: 9px solid #000; }

.inner-container {
    display: flex;
    padding-top: 30px;
    .part {
        width: calc( 100% / 4 ); } }
