/* Checkout Panel*/

.checkout-panel {
    position: absolute;
    width: 400px;
    max-height: 500px;
    background: white;
    right: 325px;
    border-radius: 9px;
    z-index: 11;
    box-shadow: 0 0 10px;
    overflow-y: scroll;
    overflow-x: hidden; }

.checkout-item {

    display: flex;
    font-family: 'Arial';
    height: 100px;
