        transition: all .4s ease;
        &:hover {
            background-color: #9b2828; } } }

.relative {
    position: absolute;
    width: 1160px;
    min-width: 1160px;
    height: 100%;
    margin: 0 auto; }

.total-price {
    display: flex;
    justify-content: space-between;
    position: relative;
