        &:hover {
            background-color: #424277; } } }




.star-container {
    display: flex;
    justify-content: flex-start;
    padding: 8px 8px 8px 14px; }

.active-star {
    transition: all .3s ease;
    color: #cc3333;
    transform: scale(1.5); }
.nonactive-star {
    transition: all .3s ease;
    color: gray;
